<template>
   <section class="about-merchant">
       <div class="main-info">
           <div class="merchant-info">
               <div class="mer">
                   <h4>{{seller.name}}</h4>
                   <div class="rate">
                       <el-rate
                        v-model="seller.score"
                        disabled
                        text-color="#ff9900"
                        score-template="{value}">
                        </el-rate>
                        <span>(661)</span><span>月售{{seller.sellCount}}单</span>
                   </div> 
               </div>
               <div class="collect">
                   <span class="heart"></span>
                   <span class="collect-title">收藏</span>
               </div>
           </div>
           <div class="peisong-info">
