                   <span>起送价</span>
                   <span class="price">{{seller.minPrice}}<span>元</span></span>
                </div>
               <div>
                   <span>商家配送</span>
                   <span class="price">{{seller.deliveryPrice}}<span>元</span></span>
                </div>
               <div>
                   <span>平均配送时间</span>
                   <span class="price">{{seller.deliveryTime}}<span>分钟</span></span>
                </div>
           </div>
       </div>
